.background {
  height: 100vh;
  width: 100vw;

  position: absolute;
  z-index: -1;

  &--top {
    position: relative;
    height: 40%;

    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }
  &--bottom {
    flex-grow: 1;
    position: relative;
  }
}

.main {
  margin: auto;

  max-width: 60%;
  display: flex;
  flex-direction: column;
  &--header {
    padding-top: 5rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    h1 {
      color: white;
      font-weight: 300;
    }
    button {
      background-color: transparent;
      border: none;
      border-radius: 50%;

      img {
        height: 30px;
        width: 30px;
      }
    }
  }
}
